import React from 'react';
import Modal from 'react-modal';

const OptionModal = (props) => (
    <Modal
        isOpen={!!props.selectedOption}
        onRequestClose={props.removeModal}
        contentLabel={'Selected Option'}
        closeTimeoutMS={200}
        className='modal'
    >
        <h3 className='modal_title'>Selected Option</h3>
        {props.selectedOption && <p className='modal_body'>{props.selectedOption}</p>}
        <button className='button' onClick={props.removeModal}>Okay</button>
    </Modal>
);

export default OptionModal;





